.Dashboard{
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 60px;
}

.Dashboard .dashboardRow{
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
    justify-content: center;
    width: 90%;
}

.Dashboard .infoSection{
    border-radius: 10px;
    background: #a2ca33;
    width: 500px;
    margin: 0px 10px;
    overflow: hidden;
}

.Dashboard .infoSection.big{
    width: 700px;
}

.Dashboard .infoSection.alert{
    background: #f16c21;
}

.Dashboard .infoSection.alert>.title{
    background: #cf591d;
}

.Dashboard .infoSection>.submitRow{
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.Dashboard .infoSection>.infoContainer{
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.Dashboard .infoSection>.vertical{
    padding-top: 20px;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.Dashboard .infoSection .imageContainer{
    width: 20%;
    overflow: hidden;
    margin-right: 10px;
}
.Dashboard .infoSection>.infoContainer>.info{
    width: 75%;
    margin-left: 10px;
}

.Dashboard .infoSection .row{
    display: flex;
    flex-direction: row;
}



.Dashboard .infoSection .imageContainer>a>img{
    width: 100%;
}

.Dashboard .infoSection>.title{
    background: #76a914;
    display: flex;
    flex-direction: row;
    padding: 10px;
    font-size: 20px;
    align-items: center;
    gap: 5px;
}

/* EDITABLE ITEM */

.Dashboard .editableItem{
    align-items: center;
}

.Dashboard .editableItem>.label{
    display: flex;
    flex-direction: row;
}

.Dashboard .editableItem>.button{
    border: 1px solid black;
    padding: 5px;
    cursor: pointer;
}

.Dashboard .iconButton{
    width: 20px;
    cursor: pointer;
}

.Dashboard .shortIdInput{
    width: 200px;
}

.Dashboard .killButton{
    padding: 10px;
    border-radius: 10px;
    margin-top: 10px;
    margin-bottom: 20px;
    cursor: pointer;
    background: #76a914;
    display: flex;
    flex-direction: row;
    gap: 5px;
    align-items: center;
}

.Dashboard .killButton:hover{
    background:#cf591d;
}

.Dashboard .infoContainer .iconLabel{
    display: flex; 
    flex-direction: row;
    align-items: center;
    margin-top: 3px;
}

.Dashboard .infoContainer .iconLabel>a{
    color: black;
}

.Dashboard .infoContainer .iconLabel>div{
    margin-right: 3px;
}

@media (max-width: 600px) {
    .Dashboard .dashboardRow{
        display: flex;
        flex-direction: column;
        margin-bottom: 0px;
    }

    .Dashboard .infoSection{
        margin-bottom: 20px;
        margin-left: 0;
        width: 100%;
    }

    .Dashboard .infoSection.big{
        width: 100%;
    }
}

.verificationButton{
    cursor: pointer;
    padding: 5px 20px;
    text-align: center;
    border-radius: 15px;
    background:#cf591d;
}

.verificationButton:hover{
    color: white;
}

.mailSentMessage{
    width: 80%;
    margin-top: 20px;
    padding: 10px;
    border-radius: 15px;
    text-align: center;
    background:#cf591d;
}

.Dashboard .errorMessage, .okMessage{
    border-radius: 15px;
    padding: 5px 10px;
    background:#cf591d;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 5px;
}

.Dashboard .errorMessage{
    color: white;
    background:#cf591d;
}

.Dashboard .okMessage{
    background: #76a914;
}

.Dashboard .killingCount{
    flex: 1;
    display: flex;
    flex-direction: row-reverse;
    font-size: 14px;
}

.Dashboard .killingCount>div{
    padding: 3px 10px;
    border-radius: 10px;
    background:#cf591d;
}

.Dashboard .sectionToggleContainer{
    flex: 1;
    display: flex;
    flex-direction: row-reverse;
}

.Dashboard .title.pointer{
    cursor: pointer;
}