.Home{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.formAlert{
    border-radius: 15px;
    margin: 10px;
    padding: 5px 10px;
    background:#cf591d;
    color: white;
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.smallLabel{
    font-size: 12px;
}

input[type=text], input[type=email], input[type=password], input[type=date], input[type=file], select {
    width: 100%;
    padding: 12px 20px;
    margin: 4px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

input[type=submit] {
    width: 100%;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}
  
input[type=submit]:hover {
    background-color: #45a049;
}
  
/* REGISTER FORM */
.HomeForm{
    background: #a2ca33;
    padding: 10px;
    border-radius: 0 0 6px 6px;
    margin-bottom: 30px;
}

.HomeForm form{
    display: flex;
    flex-direction: column;
}

.HomeForm form>.formRow{
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 12px;
}

.HomeForm form>.inputContainter+.inputContainter{
    margin-top: 5px;
}

.HomeForm form .inputContainer label{
    font-size: 15px;
}

.HomeForm .inputContainer{
    display: flex;
    flex-direction: column;
    font-size: 12px;
    flex: 1;
}

.HomeForm form>.formRow>.inputContainer+.inputContainer{
    margin-left: 10px;
}

.HomeForm form .formTitle{
    margin-top: -5px;
    font-size: 18px;
    font-weight: bold;
}

.HomeForm form .formInfo{
    font-size: 12px;
    margin-bottom: 10px;
    
}


/* TOGGLE BUTTON */
.toggle{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.toggleButton{
    padding: 10px;
    border-radius: 6px 6px 0 0;
    cursor: pointer;
    flex: 1;
    text-align: center;
    background: #a2ca33;
}

.toggleButton.notSelected{
    background: #76a914;
}

.toggleContainter{
    margin-top: 40px;
}

@media (min-width: 1500px) {
    .Home .toggleContainter{
        width: 40%;
    }
}

@media (min-width: 1000px) {
    .Home .toggleContainter{
        width: 50%;
    }
}

@media (min-width: 600px) and (max-width: 1000px) {
    .Home .toggleContainter{
        width: 70%;
    }
}

@media (max-width: 600px) {
    .Home .toggleContainter{
        width: 95%;
    }

    .HomeForm form>.formRow{
        flex-direction: column;
    }

    .HomeForm form>.formRow>.inputContainer{
        width: 100%;
    }

    .HomeForm form>.formRow>.inputContainer+.inputContainer{
        margin-left: 0px;
    }
}

/* --- */
.sectionDiv{
    border: 1px solid #76a914;
    width: 100%;
    margin: 20px 0;
}

/* MODAL */ 
.Modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.modalContainer{
    width: 50%;
    height: 70%;
    overflow-y: scroll;
    padding: 30px;
    
    background: white;
    border-radius: 30px;

    -webkit-box-shadow: 0px 0px 20px 1px rgba(0,0,0,0.47);
    -moz-box-shadow: 0px 0px 20px 1px rgba(0,0,0,0.47);
    box-shadow: 0px 0px 20px 1px rgba(0,0,0,0.47);
}

.closeModalButton{
    cursor: pointer;
}

.closeModalButton:hover{
    text-decoration: underline;
}

.underline{
    text-decoration: underline;
    cursor: pointer;
    margin-left: 3px;
}

.politicRow{
    font-size: 12px;
}