.Contact{
    margin-top: 40px;
    border-radius: 15px;
    background: #f16c21;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Contact .title{
    background:#cf591d;
    padding: 10px;
    padding-left: 30px;
    font-size: 20px;
    display: flex;
    flex-direction: row;
    gap: 5px;
    width: 100%;
    align-items: center;
}

.Contact .info{
    padding: 20px;
    text-align: center;
    max-width: 300px;
}