
.gameStats{
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-transform: uppercase;
}

.gameStats .marcador{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 250px;
    font-size: 27px;
    text-align: center;
    line-height: 25px;
    font-weight: bold;
}

.gameStats .marcador+.marcador{
    margin-left: 30px;
}

.gameStats .marcador>.label{
    padding: 10px 0;
}

.gameStats .marcador>img{
    width: 200px;
    height: 200px;
}

@media (max-width: 650px) {
    .gameStats .marcador{
        width: 150px;
        font-size: 15px;
        line-height: 20px;
    }
    .gameStats .marcador>img{
        width: 150px;
        height: 150px;
    }
}