.AdminPanel{
    padding: 10px;
    width: 100%;
    font-size: 12px;
}

@media (max-width: 1000px) {
    .AdminPanel{
        display: none;
    }    
}

#users {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
}
  
#users td, #users th {
    border: 1px solid #ddd;
    padding: 8px;
}
  
#users tr:nth-child(even){background-color: #f2f2f2;}
  
#users tr:hover {background-color: #ddd;}
  
#users .header>td {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04AA6D;
    color: white;
}

.adminControls{
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
}

.adminControls>.button{
    border: 1px solid black;
    padding: 10px;
    border-radius: 20px;
    cursor: pointer;
}

.adminControls>.button:hover{
    background: grey;
    color: white;
}

.AdminPanel .buttonCell{
    border: 2px solid black !important;
    cursor: pointer;
}

.AdminPanel .buttonCell.yes{
    background: green;
}

.AdminPanel .buttonCell.no{
    background: red;
}